import './grammar.js'
const nearley = require('nearley');
const postProcessors = require('./parserPostProcessors.js').postProcessors;



export class Parser {
  cellsReferenced = [];
  constructor(getByLabel) {
    console.log(grammar)
    this.parser = new nearley.Parser(nearley.Grammar.fromCompiled(grammar));

    // to understand it read comment in parserPostProcessors.js
    postProcessors.getByLabel = (label) => {
      const cell = getByLabel(label);
      this.cellsReferenced.push(cell);
      return cell.value;
    };

    const debug = false;
    postProcessors.log = debug ? (function () {
      console.log(...arguments)
    }) : () => {};
  }

  feed(s) {
    return this.parser.feed(s);
  }

  get results() {
    return this.parser.results[0][0];
  }
}
